import { DomHelper } from "@stechquick/shrimp/lib/helpers/domHelper";

declare var window: Window & { websdk };

const frameRoot = document.createElement("div");
const frameRootParent = document.createElement("div");
frameRoot.id= "frameRoot";

var PlateauUIOptions = {
        corsEnabled: true,
        settings: "./settings/settings_web.js"
    }

async function createSDK() {
    window["plateauUIRendererForQuickEditor"] = new Promise((res, rej) => {
        window.websdk.init(PlateauUIOptions).then(async () => {
            document.body.appendChild(frameRootParent);
            frameRootParent.appendChild(frameRoot);
            var renderer = await window.websdk.createRenderer({ mountPoint: frameRoot });
            res(renderer);
        });
    })

}



createSDK();